import React from "react";
import "./styles.css";
import Container from "@material-ui/core/Container";

import { Form, Field } from "react-final-form";
import { TextField, Checkbox, Radio, Select } from "final-form-material-ui";
import {
  Typography,
  Paper,
  Link,
  Grid,
  Button,
  CssBaseline,
  RadioGroup,
  FormLabel,
  MenuItem,
  FormGroup,
  FormControl,
  FormControlLabel,
} from "@material-ui/core";
// Picker
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  TimePicker,
  DatePicker,
} from "@material-ui/pickers";

function ExaminingBoard() {
  //

  function DatePickerWrapper(props) {
    const {
      input: { name, onChange, value, ...restInput },
      meta,
      ...rest
    } = props;
    const showError =
      ((meta.submitError && !meta.dirtySinceLastSubmit) || meta.error) &&
      meta.touched;

    return (
      <DatePicker
        {...rest}
        name={name}
        helperText={showError ? meta.error || meta.submitError : undefined}
        error={showError}
        inputProps={restInput}
        onChange={onChange}
        value={value === "" ? null : value}
      />
    );
  }

  function TimePickerWrapper(props) {
    const {
      input: { name, onChange, value, ...restInput },
      meta,
      ...rest
    } = props;
    const showError =
      ((meta.submitError && !meta.dirtySinceLastSubmit) || meta.error) &&
      meta.touched;

    return (
      <TimePicker
        {...rest}
        name={name}
        helperText={showError ? meta.error || meta.submitError : undefined}
        error={showError}
        inputProps={restInput}
        onChange={onChange}
        value={value === "" ? null : value}
      />
    );
  }

  const onSubmit = async (values) => {
    const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));
    await sleep(300);
    window.alert(JSON.stringify(values, 0, 2));
  };
  const validate = (values) => {
    const errors = {};
    if (!values.firstName) {
      errors.firstName = "Required";
    }
    if (!values.lastName) {
      errors.lastName = "Required";
    }
    if (!values.email) {
      errors.email = "Required";
    }
    return errors;
  };

  return (
    <Container maxWidth="sm" className="App">
      <div style={{ padding: 16, margin: "auto", maxWidth: 600 }}>
        <Form
          onSubmit={onSubmit}
          validate={validate}
          render={({ handleSubmit, reset, submitting, pristine, values }) => (
            <form onSubmit={handleSubmit} noValidate>
              <Paper style={{ padding: 16 }}>
                <Grid container alignItems="flex-start" spacing={2}>
                  <Grid item xs={12}>
                    <Field
                      name="titulo_trabalho"
                      fullWidth
                      required
                      component={TextField}
                      type="text"
                      label="Título do trabalho"
                    />
                  </Grid>
                  <Grid item xs={6}>
                    <Field
                      fullWidth
                      required
                      name="resumo"
                      component={TextField}
                      type="text"
                      label="Resumo"
                      multiline
                      rows={2}
                      rowsMax={10}
                    />
                  </Grid>
                  <Grid item xs={6}>
                    <Field
                      fullWidth
                      required
                      name="abstract"
                      component={TextField}
                      type="text"
                      label="Abstract"
                      multiline
                      rows={2}
                      rowsMax={10}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Field
                      name="palavras_chave"
                      fullWidth
                      required
                      component={TextField}
                      type="text"
                      label="Palavras-chave (separadas por vírgulas)"
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Field
                      fullWidth
                      name="local"
                      component={Select}
                      label="Selecione um local"
                      formControlProps={{ fullWidth: true }}
                    >
                      <MenuItem value="lab143">Laboratório 143</MenuItem>
                      <MenuItem value="auditorio">Auditório do IME</MenuItem>
                      <MenuItem value="meet">Google Meet</MenuItem>
                    </Field>
                  </Grid>
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <Grid item xs={6}>
                      <Field
                        name="data"
                        component={DatePickerWrapper}
                        fullWidth
                        margin="normal"
                        label="Data"
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <Field
                        name="horario"
                        component={TimePickerWrapper}
                        fullWidth
                        margin="normal"
                        label="Horário"
                      />
                    </Grid>
                  </MuiPickersUtilsProvider>
                  <Grid item xs={12}>
                    <Field
                      fullWidth
                      name="nota_final"
                      component={TextField}
                      type="number"
                      label="Nota final"
                    />
                  </Grid>
                  <Grid item style={{ marginTop: 16 }}>
                    <Button
                      variant="contained"
                      color="primary"
                      type="submit"
                      disabled={submitting}
                    >
                      Submeter
                    </Button>
                  </Grid>
                </Grid>
              </Paper>
              <pre>{JSON.stringify(values, 0, 2)}</pre>
            </form>
          )}
        />
      </div>
    </Container>
  );
}
export default ExaminingBoard;
